    transition:.5s;
    z-index:1
  }

  .MenuHover:before{
    content:"";
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    border-top:2px solid #262626;
    border-bottom:2px solid #262626;
    transform:scaleY(2);
    opacity:0;
    transition:.3s
  }

  .MenuHover:after{
    content:"";
    position:absolute;
    top:2px;
    left:0;
    width:100%;
    height:100%;
    background-color:#262626;
    transform:scale(0);
    opacity:0;
    transition:.3s;
    z-index:-1
  }

  .MenuHover:hover{
    color:#fff
  }

  .MenuHover:hover:before{
    transform:scaleY(1);
